.wrapper {
	min-height: 100vh;
	display: flex;
	flex-direction: column;
}

.main {
	flex-grow: 1;
}

.container {
	max-width: 985px;
	padding-left: 24px;
	padding-right: 24px;
	margin-left: auto;
	margin-right: auto;

	@include media('<md') {
		padding-left: 16px;
		padding-right: 16px;
	}
}